import React from 'react';
import { Link } from 'gatsby';
/** @jsx jsx */
import { jsx, css } from '@emotion/react';

import { images } from '../constants';

export default function Header() {
    return (
        <header css={headerStyle}>
            <Link to="/">
                <img
                    css={logoStyle}
                    src={images.chanceLogoRegular}
                    alt="chance交友軟體 logo"
                />
            </Link>
        </header>
    );
}

const headerStyle = css`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 8px 0;
    text-align: center;
    background-color: #ebe9ea;
    opacity: 0.9;
    z-index: 999;
`;

const logoStyle = css`
    max-width: 72px;
`;
